.layout-container {
  min-height: 100vh;
  background-color: #f9fafb;
}

.header {
  background: white;
  border-bottom: 1px solid #e5e7eb;
  position: sticky;
  top: 0;
  z-index: 40;
}

.sidebar {
  width: 16rem;
  background: white;
  border-right: 1px solid #e5e7eb;
  min-height: calc(100vh - 73px);
  position: sticky;
  top: 73px;
}

.nav-item {
  transition: all 0.2s ease-in-out;
}
.nav-item:hover {
  background-color: #f3f4f6;
}
.nav-item.active {
  background-color: #eff6ff;
  color: #1d4ed8;
}

.user-dropdown {
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: 0.5rem;
  width: 20rem;
  background: white;
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e7eb;
  z-index: 50;
}

.main-content {
  flex: 1;
  min-height: calc(100vh - 73px);
}

aside nav .badge,
aside nav span.badge,
aside nav .new-badge,
aside nav [class*=new-badge],
aside nav [class*=badge-new],
aside nav li > a > span.badge,
aside nav li > a > span[class*=badge]:not([ngClass]),
aside nav li > a > span[class*=rounded-full]:not([ngClass*=hidden]),
aside nav li > a > span[class*=pill]:not([ngClass*=hidden]) {
  display: none !important;
  visibility: hidden !important;
}

@media (max-width: 768px) {
  .sidebar {
    display: none;
  }
  .header .search-bar {
    display: none;
  }
}
